* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  color: #272829;
}

a {
color: #0563bb;
text-decoration: none;
}

a:hover {
color: #067ded;
text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
font-family: "Raleway", sans-serif;
}

/* Header */

#header {
position: fixed;
top: 0;
left: 0;
bottom: 0;
z-index: 9997;
transition: all 0.5s;
padding: 15px;
overflow-y: auto;
}

/* Navigation Menu */
.nav-menu {
padding: 0;
display: block;
}

.nav-menu * {
margin: 0;
padding: 0;
list-style: none;
}

.nav-menu > ul > li {
position: relative;
white-space: nowrap;
}

.nav-menu a {
display: flex;
align-items: center;
color: #45505b;
padding: 10px 18px;
margin-bottom: 8px;
transition: 0.3s;
font-size: 15px;
border-radius: 50px;
background: #f2f3f5;
height: 56px;
width: 100%;
overflow: hidden;
transition: 0.3s;
}

.nav-menu a i {
font-size: 20px;
}

.nav-menu a span {
padding: 0 5px 0 7px;
color: #45505b;
}

@media (min-width: 992px) {
.nav-menu a {
  width: 56px;
}
.nav-menu a span {
  display: none;
  color: #fff;
}
}

.nav-menu a:hover, .nav-menu .active, .nav-menu li:hover > a {
color: #fff;
background: #0563bb;
}

.nav-menu a:hover span, .nav-menu .active span, .nav-menu li:hover > a span {
color: #fff;
}

.nav-menu a:hover, .nav-menu li:hover > a {
width: 100%;
color: #fff;
}

.nav-menu a:hover span, .nav-menu li:hover > a span {
display: block;
}

section {
padding: 60px 0;
overflow: hidden;
}

.section-title {
text-align: center;
padding-bottom: 30px;
}

.section-title h2 {
font-size: 32px;
font-weight: bold;
text-transform: uppercase;
margin-bottom: 20px;
padding-bottom: 20px;
position: relative;
color: #45505b;
}

.section-title h2::before {
content: '';
position: absolute;
display: block;
width: 120px;
height: 1px;
background: #ddd;
bottom: 1px;
left: calc(50% - 60px);
}

.section-title h2::after {
content: '';
position: absolute;
display: block;
width: 40px;
height: 3px;
background: #0563bb;
bottom: 0;
left: calc(50% - 20px);
}

.section-title p {
margin-bottom: 0;
}

/* About me*/
.about .content h3 {
font-weight: 700;
font-size: 26px;
color: #728394;
}

.about .content ul {
list-style: none;
padding: 0;
}

.about .content ul li {
margin-bottom: 20px;
display: flex;
align-items: center;
}

.about .content ul strong {
margin-right: 10px;
}

.about .content ul i {
font-size: 16px;
margin-right: 5px;
color: #0563bb;
line-height: 0;
}

.about .content p:last-child {
margin-bottom: 0;
}

/* Resume */

.resume .resume-title {
font-size: 26px;
font-weight: 700;
margin-top: 20px;
margin-bottom: 20px;
color: #45505b;
}

.resume .resume-item {
padding: 0 0 20px 20px;
margin-top: -2px;
border-left: 2px solid #0563bb;
position: relative;
}

.resume .resume-item h4 {
line-height: 18px;
font-size: 18px;
font-weight: 600;
text-transform: uppercase;
font-family: "Poppins", sans-serif;
color: #0563bb;
margin-bottom: 10px;
}

.resume .resume-item h5 {
font-size: 16px;
background: #f7f8f9;
padding: 5px 15px;
display: inline-block;
font-weight: 600;
margin-bottom: 10px;
}

.resume .resume-item ul {
padding-left: 20px;
}

.resume .resume-item ul li {
padding-bottom: 10px;
}

.resume .resume-item:last-child {
padding-bottom: 0;
}

.resume .resume-item::before {
content: "";
position: absolute;
width: 16px;
height: 16px;
border-radius: 50px;
left: -9px;
top: 0;
background: #fff;
border: 2px solid #0563bb;
}

/* Projects */
.portfolio .portfolio-item {
margin-bottom: 30px;
}

.portfolio #portfolio-flters {
padding: 0;
margin: 0 auto 25px auto;
list-style: none;
text-align: center;
background: #fff;
border-radius: 50px;
padding: 2px 15px;
}

.portfolio #portfolio-flters li {
cursor: pointer;
display: inline-block;
padding: 10px 15px;
font-size: 14px;
font-weight: 600;
line-height: 1;
text-transform: uppercase;
color: #272829;
margin-bottom: 5px;
transition: all 0.3s ease-in-out;
}

.portfolio #portfolio-flters li:hover, .portfolio #portfolio-flters li.filter-active {
color: #0563bb;
}

.portfolio #portfolio-flters li:last-child {
margin-right: 0;
}

.portfolio .portfolio-wrap {
transition: 0.3s;
position: relative;
overflow: hidden;
z-index: 1;
background: rgba(69, 80, 91, 0.8);
}

.portfolio .portfolio-wrap::before {
content: "";
background: rgba(255, 255, 255, 0.7);
position: absolute;
left: 30px;
right: 30px;
top: 30px;
bottom: 30px;
transition: all ease-in-out 0.3s;
z-index: 2;
opacity: 0;
}

.portfolio .portfolio-wrap .portfolio-info {
opacity: 0;
position: absolute;
top: 0;
left: 0;
right: 0;
bottom: 0;
text-align: center;
z-index: 3;
transition: all ease-in-out 0.3s;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}

.portfolio .portfolio-wrap .portfolio-info::before {
display: block;
content: "";
width: 48px;
height: 48px;
position: absolute;
top: 35px;
left: 35px;
border-top: 3px solid #d7dce1;
border-left: 3px solid #d7dce1;
transition: all 0.5s ease 0s;
z-index: 9994;
}

.portfolio .portfolio-wrap .portfolio-info::after {
display: block;
content: "";
width: 48px;
height: 48px;
position: absolute;
bottom: 35px;
right: 35px;
border-bottom: 3px solid #d7dce1;
border-right: 3px solid #d7dce1;
transition: all 0.5s ease 0s;
z-index: 9994;
}

.portfolio .portfolio-wrap .portfolio-info h4 {
font-size: 20px;
color: #45505b;
font-weight: 600;
}

.portfolio .portfolio-wrap .portfolio-info p {
color: #45505b;
font-size: 14px;
text-transform: uppercase;
padding: 0;
margin: 0;
}

.portfolio .portfolio-wrap .portfolio-links {
text-align: center;
z-index: 4;
}

.portfolio .portfolio-wrap .portfolio-links a {
color: #45505b;
margin: 0 2px;
font-size: 28px;
display: inline-block;
transition: 0.3s;
}

.portfolio .portfolio-wrap .portfolio-links a:hover {
color: #148af9;
}

.portfolio .portfolio-wrap:hover::before {
top: 0;
left: 0;
right: 0;
bottom: 0;
opacity: 1;
}

.portfolio .portfolio-wrap:hover .portfolio-info {
opacity: 1;
}

.portfolio .portfolio-wrap:hover .portfolio-info::before {
top: 15px;
left: 15px;
}

.portfolio .portfolio-wrap:hover .portfolio-info::after {
bottom: 15px;
right: 15px;
}

/* Contact  */

.contact {
width: 100%;
background: #fff;
}

.contact i {
font-size: 20px;
color: #0563bb;
float: left;
width: 44px;
height: 44px;
background: #eef7ff;
display: flex;
justify-content: center;
align-items: center;
border-radius: 50px;
transition: all 0.3s ease-in-out;
}

.contact p {
padding: 10px 0 0 60px;
margin-bottom: 0;
font-size: 14px;
color: #728394;
}

.contact .email, .contact .phone,  .contact .linkedin, .contact .github {
margin-top: 40px;
}

.contact .email:hover i, .contact .info .linkedin:hover i, .contact .info .phone:hover i {
background: #0563bb;
color: #fff;
}

/* footer  */

footer {
background: #f7f8f9;
color: #45505b;
font-size: 14px;
text-align: center;
padding: 30px 0;
}

footer .copyright {
margin: 0 0 5px 0;
}

@media (max-width: 992px) {
#header {
  width: 300px;
  background: #fff;
  border-right: 1px solid #e6e9ec;
  left: -300px;
}

section{
  padding: 20px 0;
}

.contact .email, .contact .phone,  .contact .linkedin, .contact .github {
  margin-top: 10px;
  margin: 0 auto;
}
}

@media (min-width: 992px) {
#main {
  margin-left: 100px;
}
}
